<template>
<div>
    <div class="search-box">
        <input type="text" name="" v-model="key" class="search-input" v-on:change="search()" placeholder="搜索 所有精彩从此开始">
    </div>
    <div class="swipeBox">
        <mt-swipe :auto="4000">
            <mt-swipe-item v-for="item in swipes">
                <img v-bind:src="item.img" v-on:click="go('/view/'+item.id)">
            </mt-swipe-item>
        </mt-swipe>
    </div>
    <!-- 四个图标在这里 -->
    <ul class="icon-box">
        <li v-on:click="go('/list/%e6%9c%80%e6%96%b0%e5%b0%8f%e5%93%81')">
            <i class="iconfont icon-new">&#xe60b;</i>
            <div>最新</div>
        </li>
        <li v-on:click="go('/list/%e7%83%ad%e9%97%a8%e8%a7%86%e9%a2%91')">
            <i class="iconfont icon-hot">&#xe667;</i>
            <div>最火</div>
        </li>
        <li v-on:click="go('/man')">
            <i class="iconfont icon-man">&#xe633;</i>
            <div>分类</div>
        </li>
        <li v-on:click="deving()">
            <i class="iconfont icon-add">&#xe618;</i>
            <div>投稿</div>
        </li>
    </ul>
    <div class="list-title">今夜百乐门</div>
    <ul class="list">
        <li class="list-item" v-for="item in jyblm" v-on:click="go('/view/'+item.id)">
            <img v-lazy="item.img" src="/static/img/default.jpg">
            <div class="list-item-title">
                {{item.name}}
            </div>
        </li>
    </ul>
    <div class="list-title">岳云鹏</div>
    <ul class="list">
        <li class="list-item" v-for="item in yyp" v-on:click="go('/view/'+item.id)">
            <img v-lazy="item.img" src="/static/img/default.jpg">
            <div class="list-item-title">
                {{item.name}}
            </div>
        </li>
    </ul>
</div>
</template>
<script>
let that;
export default {
    data() {
        return {
            key: '', //搜索关键词,
            swipes: [],
            value: '',
            result: [],
            jyblm: [],
            yyp: []
        }
    },
    mounted: function() {
        that = this;
        document.title = "最小品";
        console.log(that.swipeHeight);
        that.$http.get('https://www.zuixiaopin.com/wx/getHome?t=' + (new Date().getTime() * 3 - 1048514651), {
            credentials: false
        }).then(function(res) {
            let data = JSON.parse(res.body.data);
            that.swipes = data.new;
            that.jyblm = data['今夜百乐门'];
            that.yyp = data['岳云鹏'];
            that.$indicator.close();
        }, function(e) {
            that.a = e;
        });
    },
    filters: {},
    methods: {
        go: (url) => {
            that.$indicator.open('请稍后...');
            that.$router.push({
                path: url
            });

        },
        search: () => {
            that.$indicator.open('请稍后...');
            that.$router.push({
                path: '/list/' + that.key
            });
        },
        deving: () => {
            that.$toast('酷小子拼命开发中。。');
        }

    },
    components: {
        //  Swipe
    }
}
</script>

<style lang="css">
@font-face {
  font-family: 'iconfont';  /* project id 129129 */
  src: url('//at.alicdn.com/t/font_onbs9emo6nfusor.eot');
  src: url('//at.alicdn.com/t/font_onbs9emo6nfusor.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_onbs9emo6nfusor.woff') format('woff'),
  url('//at.alicdn.com/t/font_onbs9emo6nfusor.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_onbs9emo6nfusor.svg#iconfont') format('svg');
}
.iconfont{
  font-family:"iconfont" !important;
  font-size:30px;
  font-style:normal;
  font-weight: bolder;
  line-height: 50px;
  vertical-align: middle;
}
.icon-new{
  color:#15A848;
}
.icon-hot{
  color:#f00;
}
.icon-man{
  color:#9215A8;
}
.icon-add{
  color:#A84815;
}
.icon-box{
  list-style: none;
  padding: 0px;
  margin: 10px 0px;
  display: table;
  width: 100%;
}
.icon-box li{
  display: inline-block;
 width: 25%;
 margin: 0px;
 padding:0px;
 display: table-cell;
 text-align: center;
 color: #999;
 font-size:12px;
}



</style>



