<template>
<div>
    <div class="video-box">
        <video :src="m3u8" :poster="video.img" controls></video>
    </div>
    <!-- <video-player :options="videoOptions" @player-state-changed="playerStateChanged"></video-player> -->
    <h1>《{{video.name}}》</h1>
    <div class="tag-box">
        <span class="mint-badge is-primary is-size-normal" v-on:click="goHome()">返回首页</span>
        <span class="mint-badge tag is-success is-size-normal" v-for="item in tags " v-on:click="goList(item)">
            {{item}}
        </span>
    </div>

    <div class="list-title">猜你喜欢</div>
    <ul class="list">
        <li class="list-item" v-for="item in likes" v-on:click="change(item.id)">
            <img v-lazy="item.img" src="/static/img/default.jpg">
            <div class="list-item-title">
                {{item.name}}
            </div>
        </li>
    </ul>
</div>
</template>
<script>
let that, id;
let gets = () => {
    that.$http.get('https://www.zuixiaopin.com/wx/getView?id=' + id + "&t=" + (new Date().getTime() * 3 - 1048514651), {
        credentials: false
    }).then(function(res) {
        that.video = res.body.data;
        that.m3u8 = res.body.m3u8;
        that.tags = res.body.data.tags.split(' ');
        document.title = '《' + that.video.name + '》_' + res.body.data.tags.replace(/ /g, "_");
        that.$indicator.close();
    }, function(e) {
        that.a = e;
    });
    that.$http.get('https://www.zuixiaopin.com/wx/getLikes?id=' + id + "&t=" + (new Date().getTime() * 3 - 1048514651), {
        credentials: false
    }).then(function(res) {
        if (res.body.max > 0) {
            that.likes = res.body.data;
        } else {
            that.likes = [];
        }
    }, function(e) {
        that.a = e;
    });
};
export default {
    data() {
        return {
            video: {},
            tags: [],
            m3u8: '',
            likes: []
        }
    },
    mounted: function() {
        that = this;
        id = this.$route.params.id;
        gets();
    },
    filters: {},
    methods: {
        change: (newid) => {
            id = newid;
            that.$indicator.open('请稍后...');
            that.$router.push({
                path: '/view/' + id
            });
        },
        goHome: () => {
            that.$router.push({
                path: '/'
            });
        },
        goList: (key) => {
            that.$indicator.open('请稍后...');
            that.$router.push({
                path: '/list/' + encodeURIComponent(key)
            });
        }

    },
    components: {
        //  Swipe
        //videoPlayer
    },
    watch: {
        '$route' (to, from) {
            /*当view和view之间跳转，只变了id的时候，重新获取id，不再次渲染*/
            id = this.$route.params.id;
            gets();
        }
    }
}
</script>

<style lang="css">
.video-box{
  background: #000;
}
video {
    width: 100%;
    height: 300px;
}

.tag-box{
  margin: 5px 10px 10px 10px;
}
.tag {
    margin: 2px;
}
</style>



