<template>
<div>
    <div class="search-box">
        <input type="text" name="" v-model="key" placeholder="搜索 所有精彩从此开始" class="search-input" v-on:change="search()">
        <span class="mint-badge is-primary is-size-normal search-cancle" v-on:click="goHome()">取消</span>
    </div>
    <ul class="list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
        <li class="list-item" v-for="item in lists" v-on:click="go(item.id)">
            <img v-lazy="item.img" src="/static/img/default.jpg">
            <div class="list-item-title">
                {{item.name}}
            </div>
        </li>
    </ul>
</div>

</div>
</template>
<script>
let that;
let gets = () => {
    that.$http.get('https://www.zuixiaopin.com/wx/getLists?key=' + that.key + "&page=" + that.page + "&t=" + (new Date().getTime() * 3 - 1048514651), {
        credentials: false
    }).then(function(res) {
        if (res.body.max > 0) {
            that.lists = that.lists.concat(res.body.data);
        }
        /*该结果最大页数*/
        that.maxPage = res.body.maxPage;
        that.page = res.body.page;
        that.max = res.body.max;
        /*修改title*/
        document.title = '“' + that.key + '”精彩小品搜索结果 - 最小品'
            /*看看还能不能再load*/
        if (that.page < that.maxPage) {
            that.page++;
            that.canLoad = true;
        } else {
            /*已经到了最大页*/
            that.canLoad = false;
        }

        that.$indicator.close();
    }, function(e) {
        that.a = e;
    });
};
export default {
    data() {
        return {
            key: '',
            page: 1, //记录当前的page
            lists: [],
            maxPage: 1,
            canLoad: false //锁，用来确定当前是否可以加载更多数据
        }
    },
    mounted: function() {
        that = this;
        that.key = this.$route.params.key;
        gets();
    },
    filters: {},
    methods: {
        goHome: () => {
            that.$router.push({
                path: '/'
            });
        },
        go: (id) => {
            that.$router.push({
                path: '/view/' + id
            });
            that.$indicator.open('请稍后...');
        },
        search: () => {
            that.lists = [];
            that.page = 1;
            that.canLoad = false;
            that.$indicator.open('请稍后...');
            that.$router.push({
                path: '/list/' + that.key
            });
        },
        loadMore() {
            if (that.canLoad) {
                setTimeout(function() {
                    console.log('loading more');
                    that.$indicator.open('请稍后...');
                    gets();
                }, 500);
            }
        }

    },
    components: {

    },
    watch: {
        '$route' (to, from) {
            /*当view和view之间跳转，只变了id的时候，重新获取id，不再次渲染*/
            that.key = this.$route.params.key;
            gets();
        }


    }
}
</script>

<style lang="css">
</style>



